import React, { useEffect, useState, Component } from 'react';
import { Card, Col, Row } from 'react-bootstrap';
import AuthService from '../services/AuthService';
import { parseJwt } from '../services/AuthHeader';
import { getAllBookingsByUserId } from '../services/BookingService';
import PreviousSessionCard from './PreviousSessionCard';
import UpcomingSessionCard from './UpcomingSessionCard';
import { withRouter } from 'react-router';
import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles(theme => ({
    container: {
        paddingLeft: '4rem',
        paddingRight: '4rem'
    },
    card: {
        paddingTop: '2rem',
        minHeight: '60rem',
        maxHeight: '70rem',
        height: '50rem',
        minWidth: '145rem',
        maxWidth: '145rem',
        width: '145rem'
    },
    header: {
        paddingTop: '2rem'
    },
    upcomingSessionHeader: {
        paddingLeft: '3rem'
    },
    previousSessionHeader: {
        paddingLeft: '3rem'
    },
    upcomingSessionRow: {
        height: '25rem',
        minHeight: '20rem',
        maxHeight: '30rem',
        minWidth: '145rem',
        maxWidth: '145rem',
        width: '145rem',
        overflowX: 'auto'
    },
    previousSessionRow: {
        height: '25rem',
        minHeight: '20rem',
        maxHeight: '30rem',
        minWidth: '145rem',
        maxWidth: '145rem',
        width: '145rem',
        overflowX: 'auto'
    },
    sessionColumn: {
        width: '30rem',
        minWidth: '30rem',
        maxWidth: '30rem',
        paddingLeft: '4rem',
        paddingTop: '1rem'
    },
    sessionColumnWrapper: {
        paddingLeft: '2rem',
        paddingRight: '2rem',
        paddingBottom: '1rem',
        paddingTop: '1rem'
    },
    sessionColumnCard: {
        height: '25rem',
        minHeight: '20rem',
        maxHeight: '30rem',
        minWidth: '145rem',
        maxWidth: '145rem',
        width: '145rem',
        overflowX: 'auto'
    }
}));

// /components/ListUserSessions.js
const ListUserSessions = () => {
    const [userId, setUserId] = useState('');
    const [previousSessions, setPreviousSessions] = useState([]);
    const [upcomingSessions, setUpcomingSessions] = useState([]);
    const [bookings, setBookings] = useState([]);

    useEffect(() => {
        const currentUserJwt = AuthService.getCurrentUser();

        if (currentUserJwt === null) {
            return;
        }

        const currentUser = parseJwt(currentUserJwt);
        const currentUserId = currentUser._id;
        setUserId(currentUserId);
        getAllBookingsByUserId(
            currentUserId,
            response => {
                const currentDate = new Date();
                const bookings = response.data;
                const upcomingSessions = bookings.filter(
                    session => currentDate < new Date(session.startDate)
                );
                const previousSessions = bookings.filter(
                    session => currentDate >= new Date(session.startDate)
                );

                setPreviousSessions(previousSessions);
                setUpcomingSessions(upcomingSessions);
                setBookings(bookings);
            },
            error => {
                console.error(error);
            }
        );
    }, []);

    const classes = useStyles();
    return (
        <div>
            <div className={classes.container}>
                <div className={classes.header}>
                    <h3>Manage my Bookings</h3>
                </div>

                <Card className={classes.card}>
                    <div className={classes.upcomingSessionHeader}>
                        <h4>Upcoming Sessions</h4>
                    </div>
                    <Row className={classes.upcomingSessionRow}>
                        {upcomingSessions.map((item, index) => (
                            <Col className={classes.sessionColumn}>
                                <UpcomingSessionCard
                                    value={index}
                                    session={item}></UpcomingSessionCard>
                            </Col>
                        ))}
                    </Row>
                    <hr />
                    <div className={classes.upcomingSessionHeader}>
                        <h4>Previous Sessions</h4>
                    </div>
                    <Row className={classes.previousSessionRow}>
                        {previousSessions.map((item, index) => (
                            <div className={classes.sessionColumnWrapper}>
                                <Col className={classes.sessionColumn}>
                                    <PreviousSessionCard
                                        value={index}
                                        session={item}></PreviousSessionCard>
                                </Col>
                            </div>
                        ))}
                    </Row>
                </Card>
            </div>
        </div>
    );
};

export default withRouter(ListUserSessions);
